.ysa-wrap {
    height:100%;
}

.ysa-content {
    height:100%;
    overflow:auto;
}

.ysa-container {
    @media (min-width:$screen-sm-min) {
        padding:0 30px;
    }

    @media (min-width:$screen-lg-min) {
        padding:0 60px;
    }
}

.ysa-bound {
    width:100%;
    overflow:hidden;
}