.ysa-loader {
    display:inline;

    &_overlay {
        $this:&;

        position:fixed;
        top:0;
        left:0;
        height:100%;
        width:0%;
        z-index:9999;
        overflow:hidden;

        opacity:0;
        pointer-events:none;

        transition:opacity 0.8s, width 0s 0.8s;

        &-bg {
            position:absolute;
            top:50%;
            left:50%;
            margin:-75vw 0 0 -75vw;
            height:150vw;
            width:150vw;
            min-height:150vh;
            min-width:150vh;
            transform:scale(0);

            background:$color-base;
            border-radius:50%;

            transition:transform 0s 0.5s;
        }

        &-img {
            position:absolute;
            top:50%;
            left:50%;
            margin:-36px 0 0 -25px;

            opacity:0;
            transition:opacity 0.3s;

            img {
                height:72px;
            }
        }

        &.-visible {
            opacity:1;
            width:100%;
            transition-delay:0s, 0s;
            transition-duration:0.2s, 0s;

            #{$this} {
                &-bg {
                    transform:scale(2);
                    transition:transform 1.3s;
                }

                &-img {
                    opacity:1;
                    transition-delay:0.3s;
                }
            }
        }
    }
}

@keyframes ysa-loader_overlay-triangle {
    0%, 20% {
        opacity:0;
        transform:scale(0);
    }

    40%, 70% {
        opacity:1;
        transform:scale(1);
    }

    100% {
        transform:scale(0);
    }
}