.ysa-dropdown {
    display:inline;
    position:relative;

    &_select {

        &-toggle {
            color:inherit;
            transition:opacity 0.2s;

            &-text {
                display:inline-block;
                vertical-align:middle;

                font-family:$font-family-secondary;
                font-size:18px;
                font-weight:bold;
                letter-spacing:1px;
            }

            &-arr {
                display:inline-block;
                padding:0 0 0 13px;
                vertical-align:middle;

                font-size:85%;
            }

            &:hover, &:focus {
                opacity:0.8;
                text-decoration:none;
            }
        }

        &-menu {
            display:none;
            position:absolute;
            margin:17px -30px;
            padding:10px 0;

            min-width:200px;
            max-height:250px;
            overflow:auto;
            z-index:1000;

            background:$color-base;

            &-item {
                position:relative;
                display:block;

                color:$color-text;

                font-family:$font-family-secondary;
                font-size:15px;
                font-weight:bold;
                letter-spacing:0.5px;
                line-height:35px;
                text-align:left;

                transition:background 0.2s;

                label {
                    display:block;
                    padding:3px 30px;
                    margin:0;
                    width:100%;
                    height:100%;

                    cursor:pointer;
                    font-style:normal;
                    font-weight:normal;

                    input {
                        position:absolute;
                        z-index:-999;
                        opacity:0;
                        visibility:hidden;
                    }

                    input:checked + span {
                        color:$color-primary;
                    }
                }

                &:hover {
                    background:rgba($color-text, 0.05);
                    color:$color-text;
                }
            }

            &.-right {
                right:0;
            }

            &.-aright {
                left:auto;
                right:0;

                @media (min-width:$screen-md-min) {
                    right:auto;
                }
            }
        }

        &.-open &-menu {
            display:block;
            animation:ysa-dropdown-show 0.2s;
        }
    }
}

@keyframes ysa-dropdown-show {
    0% {
        opacity:0;
        transform:translateY(-10px);
    }

    100% {
        opacity:1;
        transform:translateY(0);
    }
}