.ysa-preview {
    $root: &;

    width:100%;
    margin:20px 0;
    overflow:hidden;

    @media (min-width:$screen-sm-min) {
        margin:68px 0;
    }

    @media (min-width:$screen-lg-min) {
        margin:52px 0;
    }

    @media (min-width:$screen-xl-min) {
        margin:110px 0;
    }

    &-grid {
        width:100%;
        text-align:center;

        @media (min-width:$screen-lg-min) {
            display:table;
            table-layout:fixed;
        }

        &-left {
            direction:ltr;
            padding:32px 22px;

            @media (min-width:$screen-sm-min) {
                padding:66px 92px;
            }

            @media (min-width:$screen-lg-min) {
                display:table-cell;
                padding:110px 50px;
                vertical-align:middle;
            }

            @media (min-width:$screen-xl-min) {
                padding:147px 50px;
            }
        }

        &-right {
            direction:ltr;
            padding:20px;

            @media (min-width:$screen-sm-min) {
                padding:40px;
            }

            @media (min-width:$screen-md-min) {
                padding:40px 140px;
            }

            @media (min-width:$screen-lg-min) {
                display:table-cell;
                padding:0 50px;
                vertical-align:middle;
            }

            &.-blackout {
                background:rgba($color-text, 0.04);
            }
        }

        &.-reverse {
            direction:rtl;
        }
    }

    &-img {
        img {
            max-width:100%;
            box-shadow:0 0 74px rgba($color-text, 0.2);
        }
    }

    &-header {
        @media (min-width:$screen-lg-min) {
            margin:0 auto;
            max-width:557px;
            text-align:left;
        }

        h2, h3 {
            margin:0;
            padding:0;

            font-family:$font-family-secondary;
            font-size:24px;
            font-weight:300;
            letter-spacing:0;
            line-height:26px;

            @media (min-width:$screen-lg-min) {
                max-width:520px;
            }

            @media (min-width:$screen-sm-min) {
                font-size:40px;
                line-height:50px;
            }
        }

        hr {

        }

        &.-inverse {
            color:$color-base;
        }
    }

    &-text {
        p {
            padding:0;
            margin:0;
        }
    }

    &.-first {
        margin-top:0;
    }

    &.-last {
        margin-bottom:0;
    }
}
